* {
  font-family: 'Roboto', sans-serif;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.loginPageWrap {
  padding-top: 100px;
  /* font-size: 12px; */
  background-color: #fff9f9;
  min-height: 800px;
  min-width: 700px;
  // height: 100vh;
  // width: 100vw;
  background-color: hsla(0, 100%, 88%, 1);
  background-image: radial-gradient(at 80% 0%, hsla(189, 100%, 56%, 1) 0px, transparent 50%),
    radial-gradient(at 0% 50%, hsla(355, 100%, 93%, 1) 0px, transparent 50%),
    radial-gradient(at 80% 50%, hsla(340, 100%, 76%, 1) 0px, transparent 50%),
    radial-gradient(at 0% 100%, hsla(269, 100%, 77%, 1) 0px, transparent 50%),
    radial-gradient(at 0% 0%, hsla(343, 100%, 76%, 1) 0px, transparent 50%);
}

section {
  margin: 0 auto;
  width: 400px;
  min-height: 380px;
  transform: translateY(40%);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  background-color: rgba(255, 255, 255, 0.4);
  border-radius: 20px;
  box-shadow: 0px 0 31px 0px rgb(0 0 0 / 10%);
}

.loginFormHeading {
  margin: 20px 0 10px 0;
  font-size: 2rem;
  font-weight: 400;
  font-family: 'Poppins', sans-serif;
}

.login-form {
  width: 70%;
  margin-bottom: 15px;
}

.inputHeading {
  margin: 20px 0 5px 0;
  font-size: 1.3rem;
  font-weight: 300;
}

.loginInput {
  width: 80%;
  font-size: 1.1rem;
  font-weight: 300;
  padding: 7px;
  border: none;
  background-color: inherit;
}

.username-input,
.password-input {
  width: 90%;
  /* border-bottom: 1px solid #a4a4a4; */
}

.loginIcon {
  width: 10%;
  color: rgba(0, 0, 0, 0.3);
  padding-right: 7px;
}

.login-form>p {
  width: 90%;
  font-size: 1.1rem;
  text-align: right;
  margin-top: 5px;
  font-weight: 300;
}

.login-btn {
  border: none;
  padding: 7px 20px;
  width: 50%;
  border-radius: 10px;
  font-size: 1.2rem;
  background-image: linear-gradient(43deg,
      hsla(340, 100%, 76%, 1) 0%,
      hsla(269, 100%, 77%, 1) 100%);
  color: white;
  font-weight: 600;
}

.alternative-signup {
  width: 70%;
  margin: 20px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.alternative-signup>p {
  width: 90%;
  font-size: 1.1rem;
  text-align: center;
  margin-bottom: 5px;
  font-weight: 300;
}

.loginSpan {
  cursor: pointer;
  font-size: 1.1rem;
  font-weight: 600;
  color: hsl(269deg 26% 47%);
}

.success {
  margin-top: 10px;
  color: green;
}